*{
    margin: auto;
    padding: 0;
    box-sizing: border-box;
}

body{
    font-family: Arial, sans-serif;
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    
}
.con{
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
    flex-direction: column;
    overflow: scroll;
    scrollbar-width: none;
    row-gap: 10px;
}
.con2{
    background-color: white;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
    padding: 40px;
    width: 350px;
    height: 600px;
    text-align: center;
    border-radius: 5px;
    border: 1px solid gainsboro;
}
.con3{
    background-color: white;
    font-size: 15px;
    padding: 30px;
    width: 350px;
    text-align: center;
    border-radius: 5px;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
    border: 1px solid gainsboro;
}
h1{
    font-family: sans-serif;
    font-size: 40px;
    margin-bottom: 30px;
}
.headti{
    margin-bottom: 20px;
    font-weight: bold;
    color: rgba(92, 92, 92, 0.797);
}
input{
    border-color: rgba(0, 0, 0, 0.103);
    border: 1px solid gainsboro;
    background-color: rgba(220, 220, 220, 0.175);
    margin-bottom: 4px;
    font-size: 12px;
    display: flex;
    padding: 10px;
    width: 100%;
    border-radius: 5px;
}

.showhide{
    position: absolute;
    top: 50%;
    right: 10px;
    transform: translateY(-55%);
    background:none;
    border: none;
    font-size: 12px;
    cursor: pointer;
    color: gray;
}
.buttonshowhide{
   position: relative;
    width: 100%;
}
.sign-in-up-button-label{
    background-color: rgb(0, 136, 255);
    border: none;
    width: 100%;
    border-radius: 8px;
    cursor: pointer;
    color: rgb(255, 255, 255);
    font-size: 16px;
    padding: 6px;
    font-weight: bold;
}
.sign-in-up-button-label:hover {
    background-color: #2a75d3;
}
.para1{
    font-size: 12.2px;
    margin-top: 15px;
    color: rgb(92, 92, 92);
    
}
.para2{
    font-size: 12.2px;
    margin-top: 15px;
    margin-bottom: 15px;
    color: rgb(92, 92, 92);
}
.err{
    color: red;
    font-size: small;
    padding-bottom: 4px;
}

.link{
    text-decoration: none;
    cursor: pointer;
    color: rgb(20, 153, 243);
    font-weight: 700;
}
.cont{
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    
    flex-direction: column;
    overflow: scroll;
    scrollbar-width: none;
    row-gap: 10px;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
}
.cont2{
    background-color: white;
    padding: 40px;
    width: 350px;
    
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
    text-align: center;
    border-radius: 5px;
    border: 1px solid gainsboro;
}
.cont3{
    background-color: white;
    font-size: 15px;
    padding: 30px;
    width: 350px;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
    text-align: center;
    border-radius: 5px;
    border: 1px solid gainsboro;
}
.err1{
    color: red;
    padding: 30px;
    font-size: small;
}
.main{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 1000px;
    height: 100vh;
    text-align: center;
    margin: auto;
    background-color: white;
}
.submain{
    display: flex;
    border: none;
    border-radius: 25px;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.255);
    width: 500px;
    height: 50px;
    background-color: white;
    justify-content: space-evenly;
    margin-bottom: 75px;
}
.ex{
    border: none;
    background-color: #fff;
    font-size: large;
    cursor: pointer;
    display: flex;
}
.ex:hover {
    color: #21212198;
    font-size: 21px;
}
.ex1{
    border: 1px solid black;
    background-color: gainsboro;
    font-size: 10px;
    cursor: pointer;
    border-radius: 50%;
    padding: 6px;
    display: flex;
}
.ex1:hover {
    color: #21212198;
    font-size: 11px;
}



